/* src/App.css */
@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Vazirmatn', 'Segoe UI', 'Tahoma', 'Geneva', 'Verdana', sans-serif;
  direction: rtl;
  background: linear-gradient(135deg, #e3f2fd 0%, #f5f5f5 100%);
  min-height: 100vh;
  padding-right: 5px;
  padding-left: 5px;
}

.App {
  min-height: 100vh;
}

.header {
  text-align: center;
  margin-bottom: 30px;
  /* color: #1976d2; */
}

.headers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  margin-right: 25px;
  font-size: 2rem;
}

.header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  text-shadow: 1px 1px 2px rgba(25, 118, 210, 0.1);
  /* color: #1976d2; */
}

.headers span {
  font-size: 2rem;
  font-weight: 600;
  color: #1E429F;
}

.headers img {
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header p {
  font-size: 1.1rem;
  font-weight: 300;
  color: #3F83F8;
  opacity: 0.9;
}

.input-section {
  background: #ecf0f1;
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  text-align: center;
}

.text-input {
  width: 100%;
  max-width: 800px;
  padding: 20px;
  border: 2px solid #e1e5e9;
  border-radius: 15px;
  font-family: 'Vazirmatn', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  resize: vertical;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  direction: rtl;
}

.text-input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
  transform: translateY(-2px);
}

.submit-btn {
  background: linear-gradient(to right, #2563eb, #0a0a2a);
  color: white;
  border: 3px solid #bfdbfe;
  padding: 15px 40px;
  border-radius: 0.75rem;
  font-family: 'Vazirmatn', sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 16px rgba(25, 118, 210, 0.3);
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 12px 24px rgba(25, 118, 210, 0.4);
}

.submit-btn:disabled {
  animation: pulse 1.5s infinite;
  opacity: 0.7;
}

.error-message {
  background: #ffebee;
  color: #c62828;
  padding: 15px 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
  border: 1px solid #ffcdd2;
  font-weight: 500;
}

.graph-section {
  background: #ecf0f1;
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
}

.graph-controls {
  text-align: center;
  margin-bottom: 20px;
}

.control-btn {
  background: #f5f5f5;
  color: #424242;
  border: 2px solid #e0e0e0;
  padding: 10px 20px;
  border-radius: 25px;
  font-family: 'Vazirmatn', sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0 5px;
}

.control-btn:hover {
  background: #e3f2fd;
  border-color: #1976d2;
  color: #1976d2;
  transform: translateY(-1px);
}

.graph-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 600px;
  border: 2px dashed #bdc3c7;
  border-radius: 15px;
  background: #f8f9fa;
}

.info-section {
  background: #ecf0f1;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
}

.info-section h3 {
  color: #2c3e50;
  margin-bottom: 20px;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
}

.info-section ul {
  list-style: none;
  padding: 0;
}

.info-section li {
  padding: 10px 0;
  border-bottom: 1px solid #bdc3c7;
  color: #34495e;
  font-size: 1rem;
  position: relative;
  padding-right: 25px;
}

.info-section li:before {
  content: "✓";
  position: absolute;
  right: 0;
  color: #27ae60;
  font-weight: bold;
  font-size: 1.1rem;
}

.info-section li:last-child {
  border-bottom: none;
}

@media (max-width: 768px) {
  .App {
    padding: 15px;
  }
  .header h1 { font-size: 2rem; }
  .input-section { padding: 20px; }
  .text-input { padding: 15px; font-size: 0.9rem; }
  .submit-btn { padding: 12px 30px; font-size: 1rem; }
  .graph-container { min-height: 400px; }
}

.graph-container .node { cursor: pointer; }
.graph-container .node:hover {
  stroke: #1976d2;
  stroke-width: 2px;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.file-upload-area {
  margin-bottom: 20px;
  text-align: center;
}

.file-input {
  display: none;
}

.file-input-label {
  padding: 0.5rem 1.5rem; /* py-2 px-6 */
  background-image: linear-gradient(to right, #2563eb, #0a0a2a); /* bg-gradient-to-r from-blue-600 to-blue-950 */
  opacity: 0.8; /* opacity-80 */
  border-radius: 0.75rem; /* rounded-xl */
  font-weight: 900; /* font-black */
  font-size: 1.25rem; /* text-xl */
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25); /* shadow-2xl */
  border: 3px solid #bfdbfe; /* border-[3px] border-blue-200 */
  color: #ffffff; /* text-white */
  transition: opacity 0.2s ease-in-out; /* برای hover انیمیشن نرم */
}
file-input-label:hover{
  opacity: 1;
}

.file-input-label:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 24px rgba(25, 118, 210, 0.4);
}

.file-info {
  background: #e8f5e8;
  border: 1px solid #c8e6c9;
  border-radius: 10px;
  padding: 15px 20px;
  margin-bottom: 20px;
  text-align: center;
}

.file-info p {
  margin: 5px 0;
  color: #2e7d32;
  font-weight: 500;
}

.text-preview {
  background: #f8f9fa;
  border: 2px solid #bdc3c7;
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: right;
}

.text-preview h4 {
  color: #2c3e50;
  margin-bottom: 15px;
  font-size: 1.1rem;
  font-weight: 600;
}

.text-preview-content {
  background: white;
  border: 1px solid #e3f2fd;
  border-radius: 10px;
  padding: 15px;
  font-family: 'Vazirmatn', sans-serif;
  font-size: 0.9rem;
  line-height: 1.6;
  color: #424242;
  max-height: 200px;
  overflow-y: auto;
  direction: rtl;
  text-align: right;
}

.main-container {
  display: flex;
  gap: 30px;
  margin-bottom: 30px;
}

.graph-panel {
  flex: 2;
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(25, 118, 210, 0.1);
  border: 1px solid #e3f2fd;
  min-height: 600px;
}

.file-panel {
  flex: 1;
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(25, 118, 210, 0.1);
  border: 1px solid #e3f2fd;
  text-align: center;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.graph-panel h3,
.file-panel h3 {
  color: #1976d2;
  margin-bottom: 20px;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
}

.no-graph-message {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #424242;
  font-size: 1.1rem;
  text-align: center;
  flex-direction: column;
  gap: 10px;
}

.no-file-message {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6B7280;
  font-size: 1.1rem;
  text-align: center;
}

.graph-display {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.graph-controls {
  text-align: center;
  margin-bottom: 20px;
}

.graph-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px dashed #e3f2fd;
  border-radius: 15px;
  background: #fafafa;
  min-height: 500px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
    gap: 20px;
  }
  
  .graph-panel {
    min-height: 400px;
  }
  
  .file-panel {
    min-height: 300px;
  }
}
