/* عمومی */
.header-menu {
    background-color: #fff;
    display: flex;
    border-bottom: 2px solid #ebe5e6; /* border-b-2 */
    position: relative;
    align-items: center;
    z-index: 50;
  }
  
  .header-inner {
    width: 100%;
    position: sticky;
    display: flex;
    padding: 0rem 1.25rem; /* کاهش padding عمودی */
    margin: 0 auto;
    justify-content: space-between;
    flex-direction: row-reverse; /* mirror layout: logo at right, menu at left */
    align-items: center;
  }
  
  /* لوگو */
  .logo-container {
    display: flex;
    align-items: center;
    color: #1e40af; /* text-blue-800 */
  }
  
  .logo-container img {
    width: 4rem; /* کاهش اندازه لوگو */
    height: auto;
  }
  
  .logo-text {
    font-family: "nastaliq", serif; /* font-nastaliq */
    font-size: 2.4rem; /* کاهش اندازه فونت */
    padding: 0 0.25rem; /* px-1 */
  }
  
  /* منوی موبایل */
  .menu-toggle {
    display: flex;
    align-items: center;
  }
  
  .menu-button {
    font-size: 1.875rem; /* text-3xl */
    color: #1e40af;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  /* منوی اصلی */
  .menu {
    color: #1e40af;
    display: flex;
    flex-direction: column-reverse;
    font-family: "Byekan", sans-serif; /* font-Byekan */
    align-items: stretch;
    justify-content: space-between;
    position: absolute;
    top: 6rem; /* top-24 */
    right: 0; /* mirror anchor on mobile */
    width: 100%;
    background: #fff;
    z-index: 50;
    overflow: visible; /* allow dropdown menus to render outside container */
  }

  /* Because Tailwind is not used, define these utility-like classes */
  .menu.flex { display: flex; }
  .menu.hidden { display: none; }
  
  .menu-item {
    font-weight: 600; /* font-semibold */
    font-size: 0.875rem; /* text-sm */
    margin: 0 0.75rem; /* mx-3 */
    cursor: pointer;
    border-bottom: 2px solid #e5e7eb;
    width: 100%;
    padding: 1.25rem 1rem; /* p-5 lg:p-4 */
    border-radius: 0.375rem; /* rounded-md */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s;
  }
  
  .menu-item:hover {
    background: #e5e7eb; /* hover:bg-gray-200 */
  }
  
  /* Dropdown */
  .dropdown-trigger {
    margin: 0 0.75rem;
    cursor: pointer;
    border-bottom: 2px solid #e5e7eb;
    width: 100%;
    padding: 1.25rem 1rem;
    border-radius: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* transition: background-color 0.2s, box-shadow 0.2s; */
    /* background: #f8fafc; */
    box-shadow: 0 1px 2px rgba(0,0,0,0.04);
  }
  
  .dropdown-trigger:hover {
    background: #eef2ff;
    box-shadow: 0 6px 16px rgba(0,0,0,0.12);
  }
  
  .dropdown-trigger span {
    font-weight: 900; /* font-black */
    font-size: 1rem;
  }

  /* Custom dropdown menu */
  .custom-dropdown { position: relative; }
  .dropdown-menu {
    position: absolute;
    top: calc(100% + 6px);
    right: 0;
    min-width: 220px;
    background: #ffffff;
    border: 1px solid #e5e7eb;
    border-radius: 0.5rem;
    box-shadow: 0 12px 30px rgba(0,0,0,0.15);
    padding: 0.5rem;
    z-index: 100;
  }
  .dropdown-item {
    padding: 0.6rem 0.75rem;
    border-radius: 0.375rem;
    font-size: 0.95rem;
    color: #1f2937;
    transition: background-color 0.15s, color 0.15s;
    cursor: pointer;
  }
  .dropdown-item:hover {
    background: #f1f5f9;
    color: #111827;
  }
  
  /* آیکون خانه */
  .home-icon {
    font-size: 2rem; /* text-2xl */
    cursor: pointer;
    padding: 1.25rem 1rem;
    border-bottom: 2px solid #e5e7eb;
    border-radius: 0.375rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s;
  }
  
  .home-icon:hover {
    background: #e5e7eb;
  }
  
  /* ریسپانسیو */
  @media (min-width: 1024px) {
    .menu {
      position: static;
      top: 4rem; /* lg:top-16 */
      width: auto;
      background: transparent;
      flex-direction: row; /* keep horizontal order; container is reversed */
      align-items: center;
    }
    .menu-toggle {
      display: none; /* lg:hidden */
    }
    .menu-item,
    .dropdown-trigger,
    .home-icon {
      border: none;
      width: auto;
      padding: 1rem;
    }
    .menu-item {
      font-size: 1.125rem; /* md:text-lg */
    }
  }
